.header-section {
	position: relative;
	width: 100%;
	.scroll-icon {
		position: relative;
	}
	.bg-1 {
		position: absolute;
		bottom: 35%;
		left: 50%;
		width: 100%;
		transform: translate(-50%, 0);
		@include max-lg {
			width: 200%;
		}
		@include max-sm {
			width: 300%;
		}
	}
	.bg-2 {
		position: absolute;
		bottom: 0;
		left: 50%;
		width: 100%;
		transform: translate(-50%, 0);
		@include max-lg {
			width: 200%;
		}
		@include max-sm {
			width: 300%;
		}
	}
	.bg-3 {
		position: absolute;
		bottom: 10%;
		left: 50%;
		width: 60%;
		transform: translate(-50%);
		@include max-lg {
			width: 90%;
		}
		@include max-sm {
			width: 115%;
		}
	}
	.motif {
		position: absolute;
		left: 50%;
		width: auto;
		height: 42px;
		transform: translate(-50%, 0);
		display: flex;
		@include max-lg {
			height: 38px;
		}
		@include max-sm {
			height: 33px;
		}
		&-1 {
			top: 0;
			z-index: 3;
		}
		&-2 {
			bottom: 0;
		}
		.image-element {
			width: auto;
			height: 100%;
			img {
				width: auto;
				height: 100%;
			}
		}
	}
	.header-content {
		position: relative;
		height: 100vh;
		.header {
			position: relative;
			width: 35%;
			height: 100%;
			margin: 0 auto;
			padding: 4.5rem 0 0;
			background: linear-gradient(to bottom, var(--color-primary), transparent);
			color: var(--color-light);
			@include max-xl {
				width: 40%;
			}
			@include max-lg {
				width: 65%;
			}
			@include max-md {
				width: 80%;
			}
			@include max-sm {
				width: 100%;
			}
			.header-title {
				position: relative;
				text-align: center;
				padding: 5rem 1rem;
				@include max-md {
					padding: 3rem 1rem;
				}
				&-content {
					position: relative;
				}
			}
			#scroll-animate {
				animation: scrollAnim 1500ms ease infinite;
			}
		}
	}
}

@keyframes scrollAnim {
	0% {
		opacity: 0;
		transform: translate3d(0, -20%, 0);
	}
	50% {
		opacity: 1;
	}
	100% {
		transform: translate3d(0, 100%, 0);
		opacity: 0;
	}
}

[data-anim="zoom-in-main"].has-animate {
	animation-name: zoomInMain;
	animation-fill-mode: forwards;
	animation-duration: 3500ms !important;
}

[data-anim="peafowl-right"].has-animate {
	animation-name: peafowlRight;
	animation-duration: 3500ms !important;
	animation-fill-mode: forwards;
	animation-timing-function: linear;
}
[data-anim="peafowl-left"].has-animate {
	animation-name: peafowlLeft;
	animation-duration: 2500ms !important;
	animation-fill-mode: forwards;
	animation-timing-function: cubic-bezier(0.33, 0.12, 0.61, 0.9);
}

@keyframes peafowlRight {
	0% {
		transform: translate3d(50%, 0, 0);
	}
	20% {
		transform: translate3d(60%, 5%, 0);
	}
	40% {
		transform: translate3d(70%, 0, 0);
	}
	60% {
		transform: translate3d(80%, 5%, 0);
	}
	80% {
		transform: translate3d(90%, 0, 0);
	}
	100% {
		transform: translate3d(100%, 5%, 0);
	}
}

@keyframes peafowlLeft {
	0% {
		transform: translate3d(-50%, 0, 0);
	}
	20% {
		transform: translate3d(-60%, 5%, 0);
	}
	40% {
		transform: translate3d(-70%, 0, 0);
	}
	60% {
		transform: translate3d(-80%, 5%, 0);
	}
	80% {
		transform: translate3d(-90%, 0, 0);
	}
	100% {
		transform: translate3d(-100%, 5%, 0);
	}
}
