.reminder-wrap {
	position: relative;
	display: block;
	padding: 0 0 5rem;
	@include max-md {
		padding: 0 0 3rem;
	}
	.bg {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
	}
	.motif {
		position: absolute;
		top: 0;
		left: 50%;
		width: 300%;
		transform: translate(-50%, 0);
	}
	.reminder-content {
		position: relative;
		width: 100%;
		background-color: var(--color-primary);
		color: var(--color-light);
		border-radius: var(--border-radius);
		padding: 10rem 2rem 8rem;
		overflow: hidden;
		@include max-lg {
			padding: 8rem 2rem;
		}
		@include max-md {
			padding: 5rem 1rem;
		}
	}
	.btn-reminder {
		z-index: 3;
		position: relative;
		margin: 0 auto;
		display: flex;
		align-items: center;
		position: relative;
		gap: 0.5rem;
		transition: all 0.2s ease-in-out;
		text-decoration: none;
		margin-top: 1.5rem;
		path {
			fill: var(--text-button);
			transition: all 0.3s ease-in-out;
		}
	}
}
