.thank-section {
	position: relative;
	padding: 10rem 0;
	background-color: var(--color-primary);
	text-align: center;
	@include max-md {
		padding: 8rem 0;
	}
	.bg-1 {
		position: absolute;
		bottom: 35%;
		left: 50%;
		width: 100%;
		transform: translate(-50%, 0);
		@include max-lg {
			width: 200%;
		}
		@include max-sm {
			width: 250%;
		}
	}
	.bg-2 {
		position: absolute;
		bottom: 0;
		left: 50%;
		width: 100%;
		transform: translate(-50%, 0);
		@include max-lg {
			width: 200%;
		}
		@include max-sm {
			width: 250%;
		}
	}
	.bg-3 {
		position: absolute;
		bottom: 10%;
		left: 50%;
		width: 60%;
		transform: translate(-50%);
		@include max-lg {
			width: 90%;
		}
		@include max-sm {
			width: 115%;
		}
	}
	.motif {
		position: absolute;
		left: 50%;
		bottom: 0;
		width: auto;
		height: 42px;
		transform: translate(-50%, 0);
		display: flex;
		@include max-lg {
			height: 38px;
		}
		@include max-sm {
			height: 33px;
		}
		.image-element {
			width: auto;
			height: 100%;
			img {
				width: auto;
				height: 100%;
			}
		}
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 4/3;
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.thank-background {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}
	}
	.thank-wrapper {
		position: relative;
		.thank {
			position: relative;
			background: linear-gradient(to bottom, var(--color-main), transparent);
			&-body {
				position: relative;
				padding: 3rem 2rem;
				@include max-md {
					padding: 2rem 1rem;
				}
			}
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	background-color: var(--color-secondary);
	color: var(--text-button);
	small {
		position: relative;
	}
}
